@import "src/blocks/navigation/navigation";
@import "src/blocks/search/search";
@import "src/blocks/header/mobile";
@import "src/blocks/header/mobile-nav";

.header {
	border-bottom: 1px solid $athens-gray;
	@media screen and (min-width: $phablet-size) and (max-width: $tablet-size) {
		border-bottom: 0;
	}
	&__nav {
		&.header__nav {
			&--right {
				margin-left: auto;
			}
		}
	}
	&__top {
		background-color: $athens-gray;
		padding: 10px 0;
		@media screen and (min-width: $phone-size-small) and (max-width: $phablet-size) {
			display: none;
		}
	}
	&__content {
		align-items: center;
		display: flex;
	}
	&__main {
		padding: 25px 0 15px 0;
		@media screen and (min-width: $phone-size-small) and (max-width: $phablet-size) {
			display: none;
		}
	}
	&__phones {
		display: flex;
		margin-right: 90px;
		& > * {
			margin-right: 30px;
			&:last-child { margin-right: 0; }
		}
	}
	&__phone {
		color: $tundora;
		display: block;
		font-size: $base-font-size;
		font-weight: 600;
		white-space: nowrap;
	}
	&__link {
		color: $silver-chalice;
		display: block;
		font-size: 12px;
		line-height: 1.67;
		text-decoration: underline;
	}
	&__description {
		color: $silver-chalice;
		display: block;
		font-size: 12px;
		line-height: 1.67;
	}
	&__search {
		margin-left: auto;
	}
	&__menu {
		@media screen and (min-width: $phone-size-small) and (max-width: $tablet-size) {
			display: none;
		}
	}
	&__item {
		@media screen and (min-width: $phablet-size) and (max-width: $tablet-size) {
			display: none;
		}
		&.header__item {
			&--mobile {
				@media screen and (min-width: $phablet-size) and (max-width: $tablet-size) {
					display: block;
				}
			}
		}
	}
	&__shop {
		display: flex;
		margin-left: auto;
		flex: 0 0 270px;
		justify-content: space-between;
		width: 270px;
		& > * {
			margin-right: 10px;
			&:last-child { margin-right: 0; }
		}
	}
	&__logo {
		margin-right: 40px;
	}
	&-goods {
		&__price {
			color: $tundora;
			display: block;
			font-size: $base-font-size;
			font-weight: 600;
			white-space: nowrap;
		}
		&__count {
			color: $silver-chalice;
			display: block;
			font-size: 12px;
			line-height: 1.67;
		}
	}
	&-basket {
		align-items: center;
		background-color: $wild-sand;
		border-radius: 20px;
		cursor: pointer;
		display: flex;
		height: 40px;
		padding: 0 20px;
		&:hover {
			background-color: $alto;
			transition: all linear 0.3s;
		}
		& > * {
			margin-right: 15px;
			&:last-child { margin-right: 0; }
		}
		&__icon {
			@include size(20px);
			fill: $blaze-orange;
		}
		&__text {
			color: $tundora;
			font-weight: 500;
		}
	}
}
